import L from 'leaflet';
import 'leaflet-draw';
import {featureLayer, dynamicMapLayer, tiledMapLayer, get, post, authenticate} from 'esri-leaflet';
import {service} from 'esri-leaflet-gp';
// import {geosearch, geocodeService, arcgisOnlineProvider, geocodeServiceProvider, featureLayerProvider} from 'esri-leaflet-geocoder';
import * as esri from 'esri-leaflet-geocoder';
import {drawnItemsToJSON} from './exportGeoJSON';

// var clientID = 'MQbs5fBiwn7bT1ch';
var accessToken;
// console.log('init accessToken: ' + accessToken);

var wl = new L.FeatureGroup();

var ssl = new L.FeatureGroup();

var swl = new L.FeatureGroup();


// submit element of form
var submitBtn = document.getElementById('formSubmit');
var authPane = document.getElementById('authPane');

// add event listener to form
submitBtn.addEventListener('click', addServicesFromServer);

// var tokenUrl = 'https://sampleserver6.arcgisonline.com/arcgis/tokens/generateToken';
var tokenUrl = 'https://gis.salisbury.md/portal/sharing/rest/generateToken';

// function to make request to server
function serverAuth(server, username, password, callback) {
  post(server, {
    username: username,
    password: password,
    f: 'json',
    expiration: 86400,
    client: 'referer',
    referer: window.location.origin
  }, callback);
}

// function to run when form submitted
function addServicesFromServer(e) {
  // prevent page from refreshing
  e.preventDefault();

  // get values from form
  var username = document.getElementById('username').value;
  var password = document.getElementById('password').value;

  // generate token from server and add service from callback function
  serverAuth(tokenUrl, username, password, function(error, response) {
    // add layer to map
    var wl = dynamicMapLayer({
        url: 'https://gis.salisbury.md/arcgis/rest/services/Public_Utilities/Water/MapServer',
        opacity: 1,
        token:  response.token
    });
    var ssl = dynamicMapLayer({
        url: 'https://gis.salisbury.md/arcgis/rest/services/Public_Utilities/Sewer/MapServer',
        opacity: 1,
        token:  response.token
    });
    var swl = dynamicMapLayer({
        url: 'https://gis.salisbury.md/arcgis/rest/services/Public_Utilities/Stormwater/MapServer',
        opacity: 1,
        token:  response.token
    });
    var zoning = tiledMapLayer({
        url: 'http://gis.salisbury.md/arcgis/rest/services/LandUsePlanning/Zoning_CACHED/MapServer',
        opacity: 1,
        token:  response.token
    });
    accessToken = response.token;
    // map.addLayer(wl)
    // console.log(accessToken);
    authPane.innerHTML = '<label>'+username+'<input id="token" type="hidden" value="'+response.token+'"></label>';
    var layer_control_group = layerControl.getContainer();
    layer_control_group.style.top = "40px";
    var utilitiesLyr = L.layerGroup().addLayer(wl).addLayer(ssl).addLayer(swl).addTo(map);
    var zoningLyr = L.layerGroup().addLayer(zoning).addTo(map);

    layerControl.addOverlay(utilitiesLyr, "Utilities (Secure)");
    layerControl.addOverlay(zoningLyr, "Zoning (Secure)");
    // L.control.layers(baseLayers,overlays)._update();
  }); // end serverAuth call

  // change form html

} // end addServicesFromServer call

