//////////////////////////////////////////////////////////////////////////////////////////////
// workaround for leaflet marker not loading
// L.Icon.Default.imagePath = '.';
// OR
delete L.Icon.Default.prototype._getIconUrl;

L.Icon.Default.mergeOptions({iconRetinaUrl: require('leaflet/dist/images/marker-icon-2x.png'), iconUrl: require('leaflet/dist/images/marker-icon.png'), shadowUrl: require('leaflet/dist/images/marker-shadow.png')});
//////////////////////////////////////////////////////////////////////////////////////////////
var map = L.map('map', {
  zoomControl: true,
  editable: true,
  doubleClickZoom: false
});

//////////////////////////////////////////////////////////////////////////////////////////////
// basemap with zoom dependency
L.tileLayer('http://{s}.www.toolserver.org/tiles/bw-mapnik/{z}/{x}/{y}.png', {
  attribution: '&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors',
  maxZoom: 15,
  minZoom: 7
}).addTo(map);

var image = L.tileLayer('https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png', {
  attribution: '&copy; <a href="https://osm.org/copyright">OpenStreetMap</a> contributors',
  maxZoom: 20,
  minZoom: 15
}).addTo(map);
//////////////////////////////////////////////////////////////////////////////////////////////
// add leaflet draw controls

var editableLayers = new L.FeatureGroup();
map.addLayer(editableLayers);

var MyCustomMarker = L.Icon.extend({
  options: {
    shadowUrl: null,
    iconAnchor: new L.Point(12, 12),
    iconSize: new L.Point(24, 24),
    iconUrl: require('leaflet/dist/images/marker-icon.png')
  }
});

var options = {
  position: 'bottomleft',
  draw: {
    polyline: {
      shapeOptions: {
        color: '#f357a1',
        weight: 10
      }
    },
    polygon: {
      allowIntersection: false, // Restricts shapes to simple polygons
      drawError: {
        color: '#e1e100', // Color the shape will turn when intersects
        message: '<strong>Oh snap!<strong> you can\'t draw that!' // Message that will show when intersect
      },
      shapeOptions: {
        color: '#bada55'
      }
    },
    circle: {
      shapeOptions: {
        color: '#315e80'
      }
    }, // Turns off this drawing tool
    rectangle: {
      shapeOptions: {
        clickable: false
      }
    },
    marker: {
      icon: new MyCustomMarker()
    }
  },
  edit: {
    featureGroup: editableLayers, //REQUIRED!!
    remove: true
  }
};

var drawControl = new L.Control.Draw(options);
map.addControl(drawControl);

map.on(L.Draw.Event.CREATED, function(e) {
  var type = e.layerType,
    layer = e.layer;

  if (type === 'marker') {
    layer.bindPopup('A popup!');
  }

  editableLayers.addLayer(layer);
});

///////////////////////////////////////////////////////////////////////////////////////////
// download function
document.getElementById('exportButton').onclick = function(e) {
  // Extract GeoJson from featureGroup
  var data = editableLayers.toGeoJSON();
  // Stringify the GeoJson
  var convertedData = 'text/json;charset=utf-8,' + encodeURIComponent(JSON.stringify(data));
  // Create export
  document.getElementById('exportButton').setAttribute('href', 'data:' + convertedData);
  document.getElementById('exportButton').setAttribute('download', 'data.geojson');
  // activate.onClick();
  console.log('export button clicked');
}

//////////////////////////////////////////////////////////////////////////////////////////////
// ArcGIS Online
var agolProvider = esri.arcgisOnlineProvider({label: 'ArcGIS Online World Geocoding Service', maxResults: 6});
// SBY Composite Locator
var serverGeocode = esri.geocodeServiceProvider({
  label: 'SBY Composite Locator', maxResults: 6, attribution: 'City of Salisbury, MD: GIS', url: 'https://gis.salisbury.md/arcgis/rest/services/Composite_Locator/GeocodeServer',
  // manually declare the parameter name for single line address search supported by your custom geocoding service
  // SingleLine: 'Single Line Input'
});
// Wico Parcels
var parcelLayer = esri.featureLayerProvider({
  url: 'https://gis.salisbury.md/arcgis/rest/services/Cadastral/Parcels/MapServer/0',
  searchFields: [
    'ADDRESS', 'OWNNAME1'
  ],
  formatSuggestion: function(feature) {
    var value = feature.properties.NFMTTLVL,
      mortgage = feature.properties.MORTGAG1,
      percent = (mortgage / value) * 100,
      trunc = Math.floor(percent * 100) / 100
    return `Address: ${feature.properties.ADDRESS}</br>Owner Name: ${feature.properties.OWNNAME1}</br>NFMTTLVL: ${feature.properties.NFMTTLVL}</br>MORTGAG1: ${feature.properties.MORTGAG1}</br>Percent Underwater: ${trunc}`;
  },
  label: 'Wicomico Parcels'
});

// esri-leaflet-geocoder function {geosearch}
var searchControl = esri.geosearch({
  useMapBounds: false,
  providers: [
    agolProvider,
    // serverGeocode,
    parcelLayer
  ],
  placeholder: 'Search for an address',
  title: 'Address Search',
  // searchBounds: searchBounds,
  zoomToResult: true
}).addTo(map);

var results = L.layerGroup();

searchControl.on('results', function(data) {
  results.clearLayers();
  // for (var i = data.results.length - 1; i >= 0; i--) {
  //   results.addLayer(L.marker(data.results[i].latlng));
  // }
  if (data.results.length > 0) {
    // set map view
    map.setView(data.results[0].latlng, 15);
    // open pop-up for location
    var popup = L.popup({closeOnClick: true}).setLatLng(data.results[0].latlng).setContent(data.results[0].text).openOn(map);
  }
});
//////////////////////////////////////////////////////////////////////////////////////////////
// esri-leaflet-geocoder reverse geocodeService
var revGeocodeService = esri.geocodeService();

map.on('click', function(e) {
  revGeocodeService.reverse().latlng(e.latlng).run(function(error, result) {
    L.marker(result.latlng).addTo(results).bindPopup(result.address.Match_addr).openPopup();
  });
});
//////////////////////////////////////////////////////////////////////////////////////////////
// network analist drive time poly
// var gpService = service({url: "https://sampleserver1.arcgisonline.com/ArcGIS/rest/services/Network/ESRI_DriveTime_US/GPServer/CreateDriveTimePolygons", useCors: false});
var gpService = service({url: "https://gis.salisbury.md/arcgis/rest/services/Geoprocessing/GenerateServiceAreas/GPServer/Generate%20Service%20Areas", useCors: false});

gpService.on('authenticationrequired', function(e) {
  // oauth()
  e.authenticate(accessToken);
});

var gpTask = gpService.createTask();
// set drive time parameter for analysis
gpTask.setParam("Break_Values", "4 8");
gpTask.setParam("Break_Units", "Minutes");
gpTask.setParam("Service_Areas", "in_memory");

var driveTimes = L.featureGroup();
// map.addLayer(driveTimes);

map.on('click', function(evt) {
  // document.getElementById('info-pane').innerHTML = 'working...';
  driveTimes.clearLayers();
  gpTask.setParam("Facilities", evt.latlng)
  gpTask.run(driveTimeCallback);
});

function driveTimeCallback(error, response, raw) {
  // document.getElementById('info-pane').innerHTML = 'click on the map to calculate 1 and 2 minute drivetimes';
  driveTimes.addLayer(L.geoJSON(response.Output_Network_Analysis_Layer));
}
//////////////////////////////////////////////////////////////////////////////////////////////
//add a simple featureLayer to the map for US congressionalDistricts from MD iMAP
var criticalAreas = featureLayer({
  url: 'https://geodata.md.gov/imap/rest/services/Environment/MD_CriticalAreas/FeatureServer/0',
  simplifyFactor: 0.5,
  precision: 5,
  style: function(feature) {
    if (feature.properties.CritArea === 'Intensely Developed Area') {
      return {color: 'Crimson', weight: 2};
    } else if (feature.properties.CritArea === 'Wetland Area') {
      return {color: 'Blue', weight: 2};
    } else if (feature.properties.CritArea === 'Corporate Limit') {
      return {color: 'DarkSlateGrey', weight: 2};
    } else if (feature.properties.CritArea === 'Resource Conservation Area') {
      return {color: 'DarkOliveGreen', weight: 2};
    } else if (feature.properties.CritArea === 'Limited Development Area') {
      return {color: 'DarkOrange', weight: 2};
    } else if (feature.properties.CritArea === 'Federal Lands') {
      return {color: 'Navy', weight: 2};
    } else {
      return {color: 'Tan', weight: 2};
    }
  }
});
// map.addLayer(criticalAreas);
///////////////////////////////////////////////////////////////////////////////////////////////
//custom popup for criticalAreas
criticalAreas.bindPopup(function(layer) {
  return L.Util.template('<h6>Critical Areas:</h6></br><p>{Type}<br>{CritArea}<br>{Location}</p>', layer.feature.properties);
});
//////////////////////////////////////////////////////////////////////////////////////////////
//add a simple featureLayer/tiledMapLayer to the map for Critial Area from MD iMAP
var sbyBuildings = featureLayer({
  url: 'https://gis.salisbury.md/arcgis/rest/services/ReferenceData/Buildings/MapServer',
  simplifyFactor: 0.5,
  precision: 5,
  style: function(feature) {
    if (feature.properties.BLDG_TYPE === 'Primary Structure') {
      return {color: 'DarkGoldenrod', weight: 2};
    } else if (feature.properties.BLDG_TYPE === 'Accessory Structure') {
      return {color: 'DimGrey', weight: 2};
    } else {
      return {color: 'white', weight: 2};
    }
  }
});
// map.addLayer(sbyBuildings);
///////////////////////////////////////////////////////////////////////////////////////////////
//custom popup for sbyBuildings
sbyBuildings.bindPopup(function(layer) {
  return L.Util.template('<h4>Bruildings:</h4><p>Building Type: {BLDG_TYPE}<br>Sq.ft. {SqFt}<br><a target="_blank" href={ZONE_WEBADR}>More Info</a><br>Historic: {HISTORIC}</p>', layer.feature.properties);
});
//////////////////////////////////////////////////////////////////////////////////////////////
// color pallete
// #ffffcc
// #c7e9b4
// #7fcdbb
// #41b6c4
// #2c7fb8
// #253494
//add a simple featureLayer to the map for Critial Area from MD iMAP
var wicoParcelLayer = featureLayer({
  url: 'https://gis.salisbury.md/arcgis/rest/services/Cadastral/Parcels/MapServer',
  simplifyFactor: 0.5,
  precision: 5,
  style: function(feature) {
    var fillColor,
      mortgage = feature.properties.MORTGAG1,
      value = feature.properties.NFMTTLVL;
    if ((mortgage / value) > 1.2)
      fillColor = "#253494";
    else if ((mortgage / value) > 1.0)
      fillColor = "#2c7fb8";
    else if ((mortgage / value) > 0.8)
      fillColor = "#41b6c4";
    else if ((mortgage / value) > 0.6)
      fillColor = "#7fcdbb";
    else if ((mortgage / value) > 0.4)
      fillColor = "#c7e9b4";
    else if ((mortgage / value) > 0.2)
      fillColor = "#ffffcc";
    else
      fillColor = "#d8d8d8"; // no data
    return {color: "#999", weight: 1, fillColor: fillColor, fillOpacity: .6};
  }
});
// map.addLayer(wicoParcelLayer);
///////////////////////////////////////////////////////////////////////////////////////////////
//custom popup for wicoParcelLayer
wicoParcelLayer.bindPopup(function(layer) {
  return L.Util.template('<h4>Parcels:</h4><p>Address: {ADDRESS}</br>Owner Name: {OWNNAME1}<br>Improved Value: {NFMTTLVL}</br>Mortgage Value: {MORTGAG1}</br><a target="_blank" href={SDATWEBADR}>More Info</a></p>', layer.feature.properties);
});

///////////////////////////////////////////////////////////////////////////////////////////////
//dynamic map layer from ArcServer
var soilsLayer = dynamicMapLayer({url: 'https://services.arcgisonline.com/arcgis/rest/services/Specialty/Soil_Survey_Map/MapServer', opacity: 0.7});

// var secureLayer = featureLayer({url: 'https://gis.salisbury.md/arcgis/rest/services/Public_Utilities/Water/MapServer', opacity: 1, token: accessToken});

// secureLayer.on('authenticationrequired', function(e) {
//   // oauth()
//   console.log(accessToken);
//   e.authenticate(accessToken);
// });

///////////////////////////////////////////////////////////////////////////////////////////////
//control group function
var baseLayers = {
  // "Grayscale": gray,
  // "Streetmap": L.esri.basemapLayer("Streets")
};
var overlays = {
  "MD iMap Critical Areas": criticalAreas,
  "Salisbury Buildings": sbyBuildings,
  "Wicomico Parcels": wicoParcelLayer,
  "U.S. Soil Survey": soilsLayer,
  "4 - 8 min. Drive Time Analysis": driveTimes,
  "Forward/Reverse Geocoding Service": results,
  // "Water Utilities (Secure)": wl,
  // "Sewer Utilities (Secure)": ssl,
  // "Stormwater Utilities (Secure)": swl,
  "Leaflet Draw": editableLayers
};
var layerControl = L.control.layers(baseLayers, overlays).addTo(map);
// layerControl.addBaseLayer(baseLayers, 'Base Map');
// layerControl.addOverlay(overlays, 'Overlays');
// layerControl.addTo(map);

//////////////////////////////////////////////////////////////////////////////////////////////////
map.on('load', function() {
  console.log(`map onLoad event fired.`);
  // document.getElementById("map-loader").setAttribute('style','display: none;');
  // document.getElementById("map-loader").style.display = 'none';
});

map.setView([
  38.3607, -75.5994
], 11);



