/*
return map configuration component
*/
import '../../scripts/leafletMap.js';

export default class MapConfig extends React.Component {
  constructor(props) {
    super(props);
    this.style = {};
    this.state = {};
  }

  render() {
    var content = (
      <div>
          {/* empty component to delay loading of leaflet script until after map-container initialization */}
      </div>
      );

      return content;
    }
  };



