/*
React main page component
*/

import MapConfig from '../component/mapConfig.jsx';
// import { drawnItemsToJSON } from '../../scripts/exportGeoJSON.js';

export default class Home extends React.Component {
  constructor(props) {
    super(props);

    this.style = {};
    // this.handleChange = this.handleChange.bind(this);
    this.state = {
      loaded: false,
      display: "block"
    };
  }

  getInitialState() {
    return { loaded: false, display: "block" };
  }

  componentDidMount() {
    this.simulateLoad();
  }

  simulateLoad() {
    this.setState({ loaded: false })
    // Simulated a response callback/promise/etc.
    setTimeout(function() {
      this.onSuccess('Hello World!');
    }.bind(this), 1800);
  }

  onSuccess(display) {
    this.setState({ loaded: true, display: "none" });
  }

  render() {
    var content = (
      <div>
        <div id="map-loader" style={{"display": this.state.display}}><svg className="loading-icon"><use xlinkHref="#loading-indicator"></use></svg></div>
        <MapConfig />
        {/* <div id="map"></div> */}
        {/* <h1 className='container'>Place holder for Homepage content</h1> */}
        {/* <button href='#' id='exportButton' onClick={this.handleClick(e)}></button> */}
      </div>
    );

    return content;
  }
};



